
import { defineComponent, PropType, ref } from 'vue'
import { useStore } from 'vuex'
import { Header, ServerOptions } from 'vue3-easy-data-table'
import { ICustomerTransactionForm } from '@/types'
import useFetching from '@/hooks/useFetching'
import CustomerTransactionForm from '@/views/client/customer/forms/CustomerTransactionForm.vue'

export interface IData{
  headers: Header[],
  model: ICustomerTransactionForm,
  serverOptions: ServerOptions;
}

export default defineComponent({
  name: 'CustomerTransactionTab',

  components: {
    CustomerTransactionForm
  },

  setup () {
    const { dispatch } = useStore()

    const [loadTable, fetchStatus] = useFetching(async (id: number, payload: object) => {
      await dispatch('customer/loadTableTransaction', { id, payload })
    })

    const [createTransaction, createStatus] = useFetching(async (id: number, payload: object, payloadOptions: object) => {
      await dispatch('customer/createTransaction', { payload, id })
      await loadTable(id, payloadOptions)
    })

    return {
      form: ref(),
      fetchStatus,
      createTransaction,
      createStatus,
      loadTable
    }
  },

  props: {
    customerId: {
      type: Number as PropType<number>,
      default: 0
    }
  },

  computed: {
    dataTable () {
      return this.$store.getters['customer/getTableTransaction'] || []
    }
  },

  watch: {
    serverOptions: {
      handler: function (value) {
        this.loadServerSide(value)
      },
      deep: true
    }
  },

  data ():IData {
    return {
      headers: [
        { text: this.$t('main.labels.createdAt'), value: 'createdAt', sortable: false },
        { text: this.$t('main.labels.description'), value: 'description', sortable: false },
        { text: this.$t('main.labels.amount'), value: 'amount', sortable: false }
      ],
      model: {
        description: '',
        points: 0
      },
      serverOptions: {
        page: 1,
        rowsPerPage: 5,
        sortBy: 'id',
        sortType: 'desc'
      }
    }
  },

  mounted () {
    this.loadServerSide(this.serverOptions)
  },

  methods: {
    async addTransaction () {
      if (this.form && await this.form.validate()) {
        this.createTransaction(this.customerId, this.model, this.serverOptions)

        this.model = {
          description: '',
          points: 0
        }
      }
    },

    loadServerSide (options: ServerOptions) {
      const payload: any = {
        limit: options.rowsPerPage,
        offset: (options.page - 1) * options.rowsPerPage
      }

      if (options.sortBy && options.sortType) {
        payload.order = `${options.sortBy}:${options.sortType}`
      }

      this.loadTable(this.customerId, payload)
    }
  }
})
