
import { defineComponent, PropType } from 'vue'
import { useStore } from 'vuex'
import { Header, ServerOptions } from 'vue3-easy-data-table'
import useFetching from '@/hooks/useFetching'

export interface IData{
  headers: Header[],
  serverOptions: ServerOptions;
}

export default defineComponent({
  name: 'CustomerIpTab',

  setup () {
    const { dispatch } = useStore()

    const [loadTable, fetchStatus] = useFetching(async (id, payload) => {
      await dispatch('customer/loadTableIp', { id, payload })
    })

    return {
      fetchStatus,
      loadTable
    }
  },

  props: {
    customerId: {
      type: Number as PropType<number>,
      default: 0
    }
  },

  computed: {
    dataTable () {
      return this.$store.getters['customer/getTableIp'] || []
    }
  },

  watch: {
    serverOptions: {
      handler: function (value) {
        this.loadServerSide(value)
      },
      deep: true
    }
  },

  data ():IData {
    return {
      headers: [
        { text: this.$t('main.labels.createdAt'), value: 'createdAt', sortable: false },
        { text: this.$t('main.labels.ip'), value: 'ip', sortable: false },
        { text: this.$t('main.labels.country'), value: 'country', sortable: false }
      ],
      serverOptions: {
        page: 1,
        rowsPerPage: 5,
        sortBy: 'id',
        sortType: 'desc'
      }
    }
  },

  mounted () {
    this.loadServerSide(this.serverOptions)
  },

  methods: {
    loadServerSide (options: ServerOptions) {
      const payload: any = {
        limit: options.rowsPerPage,
        offset: (options.page - 1) * options.rowsPerPage
      }

      if (options.sortBy && options.sortType) {
        payload.order = `${options.sortBy}:${options.sortType}`
      }

      this.loadTable(this.customerId, payload)
    }
  }
})
