
import { defineComponent, PropType, ref } from 'vue'
import { useStore } from 'vuex'
import type { Header, ServerOptions } from 'vue3-easy-data-table'
import useFetching from '@/hooks/useFetching'
import CustomerHistoryForm from '@/views/client/customer/forms/CustomerHistoryForm.vue'
import { ICustomerHistoryForm } from '@/types'

export interface IData{
  headers: Header[];
  serverOptions: ServerOptions;
  model: ICustomerHistoryForm;
}

export default defineComponent({
  name: 'CustomerHistoryTab',

  components: {
    CustomerHistoryForm
  },

  setup () {
    const { dispatch } = useStore()

    const [loadTable, fetchStatus] = useFetching(async (id, payload) => {
      await dispatch('customer/loadTableHistory', { id, payload })
    })

    const [createHistory, createStatus] = useFetching(async (id, payload, payloadOptions) => {
      await dispatch('customer/createHistory', { payload, id })
      await loadTable(id, payloadOptions)
    })

    return {
      form: ref(),
      fetchStatus,
      createHistory,
      createStatus,
      loadTable
    }
  },

  props: {
    customerId: {
      type: Number as PropType<number>,
      default: 0
    }
  },

  computed: {
    dataTable () {
      return this.$store.getters['customer/getTableHistory'] || []
    }
  },

  watch: {
    serverOptions: {
      handler: function (value) {
        this.loadServerSide(value)
      },
      deep: true
    }
  },

  data ():IData {
    return {
      headers: [
        { text: this.$t('main.labels.createdAt'), value: 'createdAt', sortable: true },
        { text: this.$t('main.labels.comment'), value: 'comment', sortable: false }
      ],
      serverOptions: {
        page: 1,
        rowsPerPage: 5,
        sortBy: 'id',
        sortType: 'desc'
      },
      model: {
        comment: ''
      }
    }
  },

  mounted () {
    this.loadServerSide(this.serverOptions)
  },

  methods: {
    async addHistory () {
      if (this.form && await this.form.validate()) {
        await this.createHistory(this.customerId, this.model, this.serverOptions)

        this.model = {
          comment: ''
        }
      }
    },

    loadServerSide (options: ServerOptions) {
      const payload: any = {
        limit: options.rowsPerPage,
        offset: (options.page - 1) * options.rowsPerPage
      }

      if (options.sortBy && options.sortType) {
        payload.order = `${options.sortBy}:${options.sortType}`
      }

      this.loadTable(this.customerId, payload)
    }
  }
})
