
import { defineComponent, PropType, ref, reactive } from 'vue'
import { ICustomerGroupItem, ICustomerForm } from '@/types'
import CustomerAddressForm from '@/views/client/customer/forms/CustomerAddressForm.vue'
import CustomerAffiliateForm from '@/views/client/customer/forms/CustomerAffiliateForm.vue'
import CustomerHistoryTab from '@/views/client/customer/tabs/CustomerHistoryTab.vue'
import CustomerIpTab from '@/views/client/customer/tabs/CustomerIpTab.vue'
import CustomerTransactionTab from '@/views/client/customer/tabs/CustomerTransactionTab.vue'
import CustomerRewardTab from '@/views/client/customer/tabs/CustomerRewardTab.vue'
import { ElMessageBox, FormInstance, FormRules, FormValidationResult, Action } from 'element-plus'
import { FormRule } from '@/constants/validators'

export default defineComponent({
  name: 'CustomerForm',

  setup () {
    return {
      form: ref<FormInstance>(),
      affiliateForm: ref<FormInstance>(),
      addressForms: ref<FormInstance[]>(),
      rules: reactive<FormRules>(
        FormRule
          .templates()
          .customerGroupId(false)
          .firstName(32)
          .lastName(32)
          .middleName(32)
          .email()
          .phone()
          .close()
      )
    }
  },

  components: {
    CustomerAddressForm,
    CustomerAffiliateForm,
    CustomerHistoryTab,
    CustomerIpTab,
    CustomerTransactionTab,
    CustomerRewardTab
  },

  props: {
    modelValue: Object as PropType<ICustomerForm>,

    isLoading: Boolean as PropType<boolean>,

    isNew: {
      type: Boolean as PropType<boolean>,
      default: true
    }
  },

  computed: {
    customerGroups (): ICustomerGroupItem[] {
      return this.$store.getters['customerGroup/getItems'] || []
    }
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data () {
    return {
      model: this.modelValue || {} as any,
      generalTab: 'customer'
    }
  },

  methods: {
    messageBox (body: string) {
      ElMessageBox.alert(body, this.$t('main.messages.error'), {
        type: 'error',
        confirmButtonText: this.$t('main.buttons.ok')
      })
    },

    handleAdd (targetName: string, action: 'remove' | 'add') {
      if (action === 'remove') {
        if (targetName === 'customer') {
          this.messageBox(this.$t('customer.messages.notDeleteProfile'))
          return
        }

        if (this.model.addresses.length < 2) {
          this.messageBox(this.$t('customer.messages.notDeleteLastAddress'))
          return
        }

        const [, index] = targetName.split('_')

        this.model.addresses.splice(parseInt(index), 1)
      } else if (action === 'add') {
        this.model.addresses.push({
          firstName: '',
          lastName: '',
          middleName: null,
          company: null,
          address1: '',
          address2: null,
          city: '',
          postcode: null,
          countryId: null,
          zoneId: null,
          phone: null,
          isDefault: false
        })
      }
    },

    async validate (): FormValidationResult {
      let isValid = true
      let isValidAffiliate = true
      let isValidAddress = true

      if (this.affiliateForm) {
        await this.affiliateForm.validate((valid) => {
          isValidAffiliate = valid
        })
      }

      if (this.addressForms && Array.isArray(this.addressForms)) {
        for (const addressForm of this.addressForms) {
          await addressForm.validate((valid) => {
            if (!valid) {
              isValidAddress = false
            }
          })
        }
      }

      if (this.form) {
        await this.form.validate((valid) => {
          isValid = valid
        })
      }

      return isValid && isValidAffiliate && isValidAddress
    }
  }
})
