
import { defineComponent, PropType, reactive, ref } from 'vue'
import { ICustomerGroupItem, ICustomerHistoryForm } from '@/types'
import type { FormInstance, FormRules, FormValidateCallback, FormValidationResult } from 'element-plus'
import { FormRule } from '@/constants/validators'

export default defineComponent({
  name: 'CustomerHistoryForm',

  setup () {
    return {
      form: ref<FormInstance>(),
      rules: reactive<FormRules>(
        FormRule
          .builder()
          .field('comment')
          .string()
          .required()
          .end()
      )
    }
  },

  props: {
    modelValue: Object as PropType<ICustomerHistoryForm>
  },

  computed: {
    customerGroups (): ICustomerGroupItem[] {
      return this.$store.getters['customerGroup/getItems'] || []
    }
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data () {
    return {
      model: this.modelValue || {} as any
    }
  },

  methods: {
    async validate (callback?: FormValidateCallback | undefined): FormValidationResult {
      if (!this.form) {
        return true
      }

      return this.form.validate(callback)
    }
  }
})
