
import { defineComponent, PropType, reactive, ref } from 'vue'
import { ICustomerAddressForm } from '@/types/customer.types'
import type { FormInstance, FormRules, FormValidateCallback, FormValidationResult } from 'element-plus'
import { FormRule } from '@/constants/validators'

export interface IData {
  model: ICustomerAddressForm;
  isValid: boolean;
}

export default defineComponent({
  name: 'CustomerAddressForm',

  setup () {
    return {
      form: ref<FormInstance>(),
      rules: reactive<FormRules>(
        FormRule
          .templates()
          .firstName(32)
          .lastName(32)
          .middleName(32)
          .company()
          .address('address1', true)
          .address('address2')
          .city()
          .postcode()
          .countryId()
          .zoneId()
          .close()
      )
    }
  },

  props: {
    modelValue: Object as PropType<ICustomerAddressForm>,
    currentAddress: String
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data () : IData {
    return {
      model: this.modelValue || {
        firstName: '',
        lastName: '',
        middleName: null,
        company: null,
        address1: '',
        address2: null,
        city: '',
        postcode: null,
        countryId: null,
        zoneId: null,
        phone: null,
        isDefault: false
      },
      isValid: false
    }
  },

  methods: {
    async validate (callback?: FormValidateCallback | undefined): FormValidationResult {
      if (!this.form) {
        return true
      }

      return this.form.validate(callback)
    }
  }
})
