
import { defineComponent, PropType, reactive, ref } from 'vue'
import { ICustomerAffiliateForm } from '@/types/customer.types'
import type { FormInstance, FormRules, FormValidateCallback, FormValidationResult } from 'element-plus'
import { FormRule } from '@/constants/validators'

export interface IData{
  model: ICustomerAffiliateForm;
  payment: any[];
  isValid: boolean;
}
export default defineComponent({
  name: 'CustomerAffiliate',

  setup () {
    return {
      form: ref<FormInstance>(),
      rules: reactive<FormRules>(
        FormRule
          .builder()
          .field('company')
          .string()
          .field('website')
          .url()
          .field('cheque')
          .required()
          .string()
          .field('payPal')
          .required()
          .string()
          .field('bankAccountName')
          .required()
          .string()
          .field('bankAccountNumber')
          .required()
          .string()
          .end()
      )
    }
  },

  props: {
    modelValue: Object as PropType<ICustomerAffiliateForm>
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data () : IData {
    return {
      model: this.modelValue || {
        company: '',
        website: '',
        tracking: '',
        commission: '5.00',
        tax: '',
        payment: 'bank',
        cheque: null,
        payPal: null,
        bankName: null,
        bankBranchNumber: null,
        bankSwiftCode: null,
        bankAccountName: null,
        bankAccountNumber: null,
        customField: '',
        createdAt: '',
        isActive: false
      },
      payment: [
        { id: 'cheque', name: 'cheque' },
        { id: 'paypal', name: 'paypal' },
        { id: 'bank', name: 'bank' }
      ],
      isValid: false
    }
  },

  methods: {
    async validate (callback?: FormValidateCallback | undefined): FormValidationResult {
      if (!this.form) {
        return true
      }

      return this.form.validate(callback)
    }
  }
})
